<template>
  <div :class="{app: true, flex_Full: fullScreen}">
    <div class="left" v-if="fullScreen"> <!-- add for full screen -->
      <Menufull :arrow="showArrow"></Menufull>
    </div>
    <div class="right" @scroll="syka()">
      <Up v-if="!fullScreen"></Up>
      <router-view></router-view>
      <About></About>
      <Services></Services>
      <Faq></Faq>
      <Evro></Evro>
      <AboutBig></AboutBig>
      <Contact></Contact>
      <FooterDelta></FooterDelta>
    </div>
  </div>
</template>


<script>
import Up from "./components/Up";
import Faq from "@/components/Faq";
import Evro from "@/components/Evro";
import Contact from "@/components/Contact";
import FooterDelta from "@/components/Footer-delta";
import About from "@/components/About";
import AboutBig from "@/components/AboutBig";
import Services from "@/components/Services";
import Menufull from "@/components/Menufull";
export default {
  name: 'App',
  data() {
    return {
      background: '',
      text: '',
      alert: '',
      colorBlock: '',
      showColor: false,
      fullScreen: false,
      showArrow: false
    }
  },
  created() { //add for full screen
    if (this.checkScreen() >= 1366) {
      this.fullScreen = true;
    }
    if (this.fullScreen === true) {
      window.addEventListener('scroll', ()=> {
        if (window.pageYOffset >= 50) {
          this.showArrow = true;
        }else {
          this.showArrow = false;
        }
      });
    }

  },
  components: {
    Menufull, //add for full screen
    Services,
    AboutBig,
    About,
    FooterDelta,
    Contact,
    Evro,
    Faq,
    Up,
  },
  methods: {
    checkScreen: function  () { //add for full screen
      return window.innerWidth;
    },
  }
}
</script>



