      <div class="info__carousel padding" v-for="item in aboutInfo" :key="item.name">
        <transition-group name="slider" tag="p" mode="in-out">
            <span class="info__text" v-for="el in item.text" :key="el.message" v-show="showSlider">
              {{ el.message }}
            </span>
        </transition-group>
      </div>
    </div>
    <div class="slash"><img src="../assets/media/slash.png" alt="-" class="slash__img"></div>
  </section>
</template>

<script>
export default {
  name: "About",
  data() {
    return {
      aboutInfo: [
        {
          name: 'expert',
          text: [{message: '–ü–æ–º–æ–≥–∞–µ–º –≤–æ–¥–∏—Ç–µ–ª—è–º –Ω–∞ –º–µ—Å—Ç–µ –î–¢–ü —Å 1998 –≥.'}, {message: '50 –∞—Ç—Ç–µ—Å—Ç–æ–≤–∞–Ω–Ω—ã—Ö —ç–∫—Å–ø–µ—Ä—Ç–æ–≤'}, {message: '–ù–∞–º –¥–æ–≤–µ—Ä—è—é—Ç VIP-–∫–ª–∏–µ–Ω—Ç–æ–≤: –ò–Ω–≥–æ—Å—Ç—Ä–∞—Ö, –°–û–ì–ê–ó, –†–æ—Å–≥–æ—Å—Å—Ç—Ä–∞—Ö, –°–æ–≥–ª–∞—Å–∏–µ, –†–µ–Ω–µ—Å—Å–∞–Ω—Å'}, {message: '–û—Ñ–æ—Ä–º–ª—è–µ–º >200 –∞–≤–∞—Ä–∏–π –≤ –¥–µ–Ω—å'}]
        },
        {
          name: 'company',
          text: [{message: '–£–Ω–∏–∫–∞–ª—å–Ω—ã–º–∏ —Å—Ç—Ä–∞—Ö–æ–≤—ã–º–∏ –ø—Ä–æ–≥—Ä–∞–º–º–∞–º–∏, –∑–∞—Ç–æ—á–µ–Ω–Ω—ã–º–∏ –ø–æ–¥ —Ç—Ä–µ–±–æ–≤–∞–Ω–∏—è —Å–∞–º—ã—Ö —Ä–∞–∑–Ω—ã—Ö –∫–ª–∏–µ–Ω—Ç–æ–≤.'}, {message: '–ú–æ–±–∏–ª—å–Ω–æ–π —Å–ª—É–∂–±–æ–π –∞–≤–∞—Ä–∏–π–Ω—ã—Ö –∫–æ–º–∏—Å—Å–∞—Ä–æ–≤, –ø–æ–∫—Ä—ã–≤–∞—é—â–µ–π —Ç–µ—Ä—Ä–∏—Ç–æ—Ä–∏—é –ú–æ—Å–∫–≤—ã –∏ –ú–æ—Å–∫–æ–≤—Å–∫–æ–π –æ–±–ª–∞—Å—Ç–∏'}, {message: 'Call-—Ü–µ–Ω—Ç—Ä–æ–º, –≤ –∫–æ—Ç–æ—Ä–æ–º –∫—Ä—É–≥–ª–æ—Å—É—Ç–æ—á–Ω–æ –º–æ–∂–Ω–æ –ø–æ–ª—É—á–∏—Ç—å –ø—Ä–∞–≤–æ–≤—É—é –∫–æ–Ω—Å—É–ª—å—Ç–∞—Ü–∏—é.'}, {message: '–≤—ã—Å–æ–∫–æ—Ç–µ—Ö–Ω–æ–ª–æ–≥–∏—á–Ω—ã–º–∏ —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–∞–º–∏ ‚Äì —É–Ω–∏–∫–∞–ª—å–Ω—ã–º–∏ –¥–ª—è —Ä—ã–Ω–∫–∞ –∞–≤—Ç–æ—Å—Ç—Ä–∞—Ö–æ–≤–∞–Ω–∏—è.'}]
        },
