      ],
      showSlider: true,
    }
  },
  mounted: function () {
    setInterval(() => {
      this.showSlider = !this.showSlider;
      this.nextSlider();
      setTimeout(() => {
        this.showSlider = !this.showSlider
      }, 2000);
    }, 15000);
  },
  methods: {
    nextSlider: function () {
      let arr = [];
      this.aboutInfo.forEach((item, index) => {
        if (index === this.aboutInfo.length - 1) {
          arr[0] = item;
        } else {
          arr[index + 1] = item;
        }
      })
      return this.aboutInfo = arr;
    }
  },
